<template>
  <div>
    <div class="userContact">
      <div class="wrap">
        <header class="header">
          <span><i class="fas fa-file-contract"></i></span>
          <h3></h3>
        </header>
        <div class="container">
          <div class="row">
            <div class="col-12">
              <div class="location">
                <div class="company">
                  <h2></h2>
                  <p class="address"><span>1967(C)</span></p>
                  <p><span>02-0000-0000</span></p>
                  <p><span>02-0000-0000</span></p>
                  <p><span>0912-345678</span></p>
                  <p><span>service@gmail.com</span></p>
                </div>
                <iframe
                  src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d1807.1613726459457!2d121.6148286!3d25.0570479!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3442ab5881ed5a01%3A0xf1fd9515ad913fc4!2zMTE15Y-w5YyX5biC5Y2X5riv5Y2A5LiJ6YeN6LevMTktNuiZnw!5e0!3m2!1szh-TW!2stw!4v1570272206483!5m2!1szh-TW!2stw"
                  width="70%" height="450" frameborder="0" style="border:0;" allowfullscreen=""></iframe>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-12">
              <div class="message">
                <div class="name"><i class="far fa-comment-alt"></i></div>
                <p class="ex"></p>
                <form action="#" class="form" @submit.prevent="sendForm">
                  <div class="inputRow">
                    <label for="name">*</label>
                    <input type="text" name="name" :class="{'error':errors.has('name')}" id="name"
                      placeholder="" v-validate="'required'">
                  </div>
                  <span class="errorMsg" v-if="errors.has('name')"> </span>
                  <div class="inputRow">
                    <label for="contact">*</label>
                    <input type="tel" name="tel" :class="{'error':errors.has('tel')}" id="contact" placeholder=""
                      v-validate="'required|digits:10'">
                    <input type="email" name="mail" id="contact" placeholder="Email ()">
                  </div>
                  <span class="errorMsg" v-if="errors.has('tel')">{{errors.first('tel').replace(/tel/,'')}}</span>
                  <div class="inputRow">
                    <label for="remark" class="remark">*</label>
                    <textarea name="remark" id="remark" :class="{'error':errors.has('remark')}" cols="30" rows="8"
                      placeholder="" v-validate="'required'"></textarea>
                  </div>
                  <span class="errorMsg"
                    v-if="errors.has('remark')">{{errors.first('remark').replace(/remark/,'')}}</span>
                  <div class="inputRow">
                    <button class="btn"></button>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer class="footer">
        <div class="link">
          <a href="https://reurl.cc/oD15r3"><i class="fab fa-github"></i></a>
          <a href="https://reurl.cc/9zGRvv"><i class="fab fa-codepen"></i></a>
        </div>
        <p></p>
      </footer>
    </div>
  </div>
</template>

<script>
  export default {
    methods: {
      sendForm() {
        this.$validator.validate().then(valid => {
          if (valid) {
            this.$bus.$emit('message:push', '', 'success');
          } else {
            this.$bus.$emit('message:push', '', 'danger');
          }
        });
      },
    }
  }

</script>



