<template>
  <div>
    <loading :active.sync="effect.isLoading">
      <template slot="default">
        <div class="loading-pulse"></div>
      </template>
    </loading>
    <div class="adminProductList">
      <div class="addProduct">
        <button class="btn btn--add" @click="openModal('add')"></button>
      </div>
      <table>
        <thead>
          <tr>
            <th class="t-m" style="text-align: left"></th>
            <th class="t-l"></th>
            <th class="t-m"></th>
            <th class="t-m"></th>
            <th class="t-s"></th>
            <th class="t-m"></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in products" :key="index">
            <td class="t-m" style="text-align: left">{{item.category}}</td>
            <td class="t-l">{{item.title}}</td>
            <td class="t-m">{{item.origin_price | currency}}</td>
            <td class="t-m">{{item.price | currency}}</td>
            <td class="t-s">
              <span v-if="item.is_enabled == 1" class="text-up"></span>
              <span v-else></span>
            </td>
            <td class="btn-group">
              <button class="btn btn--edit" @click="openModal('edit',item)"></button>
              <button class="btn btn--remove ml-3" @click="openModal('delete',item)"></button>
            </td>
          </tr>
        </tbody>
      </table>
      <!-- Pagination -->
      <Pagination :paginationData="pagination" @updatePagination="getProducts" v-if="pagination.current_page !== 0">
      </Pagination>
    </div>
    <!--  -->
    <UpdateModal @callUpdate="updateProduct" :item="thisItem" />
  </div>
</template>

<script>
  // 
  import UpdateModal from './pageModules/UpdateModal';
  import Pagination from './pageModules/Pagination';
  // jquery
  import $ from 'jquery';

  export default {
    components: {
      UpdateModal,
      Pagination,
    },
    data() {
      return {
        // 
        products: [],
        // 
        doing: '',
        // 
        thisItem: {},
        // Lodaing
        effect: {
          isLoading: false,
        },
        // Pagination
        pagination: {},

      }
    },
    methods: {
      // 
      getProducts(page = 1) {
        const url = `${process.env.API_Server}/api/${process.env.API_Path}/admin/products?page=${page}`;
        const vm = this;
        vm.effect.isLoading = true;
        vm.$http.get(url).then((response) => {
          // 
          if (response.data.success) {
            vm.products = response.data.products;
            vm.pagination = response.data.pagination;
          } else {
            vm.$router.push('/login');
            vm.$bus.$emit('message:push', response.data.message, 'danger');
          }
          vm.effect.isLoading = false;
        })
      },
      // 
      openModal(mode, item) {
        const vm = this;
        vm.doing = mode;
        switch (vm.doing) {
          case 'add':
            vm.thisItem = {};
            $('#productModal').modal('show');
            break;
          case 'edit':
            vm.thisItem = Object.assign({}, item);
            $('#productModal').modal('show');
            break;
          case 'delete':
            vm.thisItem = Object.assign({}, item);
            $('#delDataModal').modal('show');
            break;
        }
      },
      // 
      updateProduct(cacheProduct) {
        // console.log(cacheProduct);
        const vm = this;
        let url, httpMehods;
        switch (vm.doing) {
          case 'add':
            url = `${process.env.API_Server}/api/${process.env.API_Path}/admin/product`;
            httpMehods = 'post';
            break;
          case 'edit':
            url = `${process.env.API_Server}/api/${process.env.API_Path}/admin/product/${cacheProduct.id}`;
            httpMehods = 'put';
            break;
          case 'delete':
            url = `${process.env.API_Server}/api/${process.env.API_Path}/admin/product/${cacheProduct.id}`;
            httpMehods = 'delete';
            break;
        }
        this.$http[httpMehods](url, {
          data: cacheProduct
        }).then((response) => {
          $('#productModal').modal('hide');
          $('#delDataModal').modal('hide');
          vm.$bus.$emit('message:push', response.data.message, 'success');
          vm.getProducts();
        })
      }
    },
    created() {
      this.getProducts();
    },
  }

</script>



