<template>
  <div>
    <!--  - Product -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image" placeholder="" v-model="tempData.imageUrl">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <i class="fas fa-spinner fa-spin" v-if="status"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile">
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" alt="" :src="tempData.imageUrl">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder="" v-model="tempData.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category" placeholder=""
                      v-model="tempData.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit" placeholder="" v-model="tempData.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price" placeholder=""
                      v-model="tempData.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price" placeholder="" v-model="tempData.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description" placeholder=""
                    v-model="tempData.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content" placeholder=""
                    v-model="tempData.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled" v-model="tempData.is_enabled"
                      :true-value="1" :false-value="2">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="callUpdate"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  - Coupon -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title" placeholder="" v-model="tempData.title">
                </div>

                <div class="form-group">
                  <label for="code"></label>
                  <input type="text" class="form-control" id="code" placeholder="" v-model="tempData.code">
                </div>

                <div class="form-group">
                  <label for="date"></label>
                  <input type="date" class="form-control" id="date" max="2100-12-31" v-model="tempData.due_date">
                </div>

                <div class="form-group">
                  <label for="percent"></label>
                  <input type="text" class="form-control" id="percent" placeholder=""
                    v-model="tempData.percent">
                </div>

                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled" :true-value="1" :false-value="2"
                      v-model="tempData.is_enabled">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="callUpdate"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="modal fade" id="delDataModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span v-if="tempData.percent"></span>
              <span v-else></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempData.title }}</strong>
            <span v-if="tempData.percent"></span>
            <span v-else></span>
            ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="callUpdate"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    props: ['item'],
    data() {
      return {
        // 
        cacheData: {},
        // 
        status: false,
      }
    },
    methods: {
      // 
      callUpdate() {
        const vm = this
        vm.$emit('callUpdate', vm.cacheData);
      },
      // 
      uploadFile() {
        const url = `${process.env.API_Server}/api/${process.env.API_Path}/admin/upload`;
        const vm = this;
        const uploadFile = this.$refs.files.files[0];
        const uploadId = this.$refs.files.id;
        //  - //key/value
        const formData = new FormData();
        formData.append('file-to-upload', uploadFile);
        // end
        vm.status = true;
        vm.$http.post(url, formData, {
          'Content-Type': 'multipart/form-data',
        }).then((response) => {
          if (!response.data.success) {
            vm.$bus.$emit('message:push', response.data.message, 'danger');
          }
          vm.$set(vm.cacheData, 'imageUrl', response.data.imageUrl);
          document.getElementById(uploadId).value = '';
          vm.status = false;
        })
      }
    },
    computed: {
      // 
      tempData() {
        const vm = this;
        vm.cacheData = vm.item;
        return vm.cacheData;
      },
    }
  }

</script>

<style lang="scss" scoped>
  @import '~bootstrap/scss/bootstrap';

  .btn-primary {
    color: #fff;
    background-color: rgb(76, 167, 76);
    border-color: rgb(76, 167, 76);

    &:hover {
      background-color: rgb(62, 134, 62);
      border-color: rgb(62, 134, 62);
    }

    &:active {
      background-color: rgb(62, 134, 62) !important;
      border-color: rgb(62, 134, 62) !important;
      box-shadow: 0 0 0 0.2rem rgb(62, 134, 62) !important;
    }

    &:focus {
      box-shadow: 0 0 0 0.2rem rgb(62, 134, 62);
    }
  }

</style>



