<template>
  <div>
    <div class="userCoupon">
      <div class="wrap">
        <header class="header">
          <span><i class="fas fa-tags"></i></span>
          <h3></h3>
        </header>
        <div class="container">
          <div class="row">
            <div class="col-12">
              <div class="coupon">
                <h3 class="discount">SAVE<span>10%</span>*</h3>
                <p class="content">9</p>
                <p class="alert" :class="{'alert--open':getCoupon}"></p>
                <p class="code" :class="{'code--open':getCoupon}">sdf9fwef7</p>
                <button class="btn" :class="{'btn--close':getCoupon}" @click="getCoupon = true"></button>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col-12">
              <div class="couponGame">
                <div class="name">
                  <i class="fas fa-gamepad"></i>
                  <p></p>
                </div>
                <div class="ex">
                  <p></p>
                  <p></p>
                  <p></p>
                  <span></span>
                </div>
                <div class="scratchCard" :class="{'scratchCard--active':couponGame =='get' }">
                  <h3 class="title"></h3>
                  <div class="card">
                    <scratch-card :key="scratchCard.renderCount" :cardWidth="scratchCard.cardWidth"
                      :cardHeight="scratchCard.cardHeight" :finishPercent="scratchCard.finishPercent"
                      imageUrl="https://raw.githubusercontent.com/awdr74100/Learning-process/master/others/colorBackground/bg2.jpg"
                      :brushUrl="scratchCard.brushUrl" :forceReveal="scratchCard.forceReveal" @complete="alertCoupon">
                      <div class="card-content" v-if="randomCoupon[scratchCard.renderCount].discount !== 0">
                        <p>{{randomCoupon[scratchCard.renderCount].discount}}</p>
                        <span>{{randomCoupon[scratchCard.renderCount].code}}</span>
                      </div>
                      <div class="card-content" v-else>
                        <p></p>
                      </div>
                    </scratch-card>
                  </div>
                </div>
                <button class="btn btn--start" @click="couponGame = 'get'"
                  v-if="couponGame == 'start' && scratchCard.renderCount <2"></button>
                <button class="btn btn--get" @click="scratchCard.renderCount++"
                  v-else-if="scratchCard.renderCount >=0 && scratchCard.renderCount <=1 && couponGame == 'get' ">{{2 - scratchCard.renderCount}}</button>
                <button class="btn btn--end" v-else></button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer class="footer">
        <div class="link">
          <a href="https://reurl.cc/oD15r3"><i class="fab fa-github"></i></a>
          <a href="https://reurl.cc/9zGRvv"><i class="fab fa-codepen"></i></a>
        </div>
        <p></p>
      </footer>
    </div>
  </div>
</template>

<script>
  // vue-scratchCard
  import ScratchCard from 'vue-scratchcard';
  // 
  import BRUSH from 'vue-scratchcard/example/brush.png';

  export default {
    components: {
      ScratchCard,
    },
    data() {
      return {
        scratchCard: {
          // renderCount: parseInt(localStorage.getItem('couponNumber')) || 0,
          renderCount: 0,
          cardWidth: 400,
          cardHeight: 100,
          finishPercent: 50,
          brushUrl: BRUSH,
          forceReveal: false,
        },
        getCoupon: false,
        couponGame: 'start',
        randomCouponIndex: 0,
        randomCoupon: [],
      }
    },
    methods: {
      alertCoupon() {
        // alert('');
        const vm = this;
        let discount = vm.randomCoupon[vm.scratchCard.renderCount].discount;
        if (discount == 0) {
          vm.$bus.$emit('message:push', `QQ`, 'danger');
        } else {
          vm.$bus.$emit('message:push', `${discount}`, 'success');
        }
      },
      // 
      randomGetCoupon() {
        // AdminAjax
        const objectData = [{
          code: 'idojerjhej',
          discount: 75,
        }, {
          code: 'afwegwegw',
          discount: 8,
        }, {
          code: 'nwbrtbrtb',
          discount: 85,
        }, {
          code: '',
          discount: 0,
        }, {
          code: '',
          discount: 0,
        }, {
          code: '',
          discount: 0,
        }];
        const vm = this;
        let objectDataLen = objectData.length;
        let object = {};
        let itemLen = 3;
        while (vm.randomCoupon.length < itemLen) {
          let r = parseInt(Math.random() * objectDataLen);
          if (!object[r]) {
            object[r] = true;
            vm.randomCoupon.push(objectData[r]);
          }
        }
      }
    },
    //  LocalStorge
    watch: {
      'scratchCard.renderCount'() {
        let strCount = this.scratchCard.renderCount.toString();
        localStorage.setItem('couponNumber', strCount);
      }
    },
    created() {
      this.randomGetCoupon();
    },
  }

</script>



