<template>
  <div>
    <div class="userSidebar">
      <div class="logo">
        <router-link to="/"></router-link>
      </div>
      <input type="text" name="search" id="" placeholder="" v-model="orderId" @keyup.enter="searchOrder">
      <ul class="list">
        <li>
          <router-link to="/"></router-link>
        </li>
        <li>
          <router-link to="/About"></router-link>
        </li>
        <li>
          <a href="#" @click.prevent="dropdownOpen = !dropdownOpen"><i class="fas fa-chevron-down arrowLink"
              :class="{'arrowLink--active':dropdownOpen}"></i></a>
          <ul class="dropdownList" :class="{'dropdownList--active':dropdownOpen}">
            <li>
              <router-link to="/ProductList/"></router-link>
            </li>
            <li>
              <router-link to="/ProductList/"></router-link>
            </li>
            <li>
              <router-link to="/ProductList/"></router-link>
            </li>
            <li>
              <router-link to="/ProductList/"></router-link>
            </li>
            <li>
              <router-link to="/ProductList/"></router-link>
            </li>
            <li>
              <router-link to="/ProductList/"></router-link>
            </li>
          </ul>
        </li>
        <li>
          <router-link to="/News"></router-link>
        </li>
        <li>
          <router-link to="/Coupon"></router-link>
        </li>
        <li>
          <router-link to="/Adopt"></router-link>
        </li>
        <li>
          <router-link to="/Contact"></router-link>
        </li>
        <li>
          <router-link to="/Checkout" class="mt-30"><i class="fas fa-shopping-cart"></i></router-link>
        </li>
        <li>
          <router-link to="/admin/productList"><i class="fas fa-user-cog"></i></router-link>
        </li>
      </ul>
      <div class="privacy">
        <h4></h4>
        <p>Copyright  2019 by Roya</p>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        dropdownOpen: false,
        orderId: '',
      }
    },
    methods: {
      searchOrder() {
        const vm = this;
        let orderId = vm.orderId
        if (orderId == '') {
         vm.$bus.$emit('message:push', '', 'danger');
        } else {
          vm.$router.push({
            path: `/Checkout`,
            query: {
              id: orderId
            },
          })
        }
        vm.orderId = '';
      },
    },
  }

</script>



