<template>
  <div>
    <loading :active.sync="effect.isLoading">
      <template slot="default">
        <div class="loading-pulse left-125"></div>
      </template>
    </loading>
    <div class="userHome">
      <!-- Swiper -->
      <section class="swiper-container">
        <Swiper />
        <div class="message"><span> x </span><br>
          </div>
        <div class="prompt">
          <i class="fas fa-angle-double-down"></i>
          <p>Scroll Down</p>
          <i class="fas fa-angle-double-down"></i>
        </div>
      </section>
      <div class="wrap">
        <div class="container">
          <!-- classCard -->
          <div class="row">
            <div class="col-4">
              <div class="classCard">
                <router-link to="/ProductList/" class="card">
                  <div class="img img--class1"></div>
                </router-link>
              </div>
            </div>
            <div class="col-4">
              <div class="classCard">
                <router-link to="/ProductList/" class="card">
                  <div class="img img--class2"></div>
                </router-link>
              </div>
            </div>
            <div class="col-4">
              <div class="classCard">
                <router-link to="/ProductList/" class="card">
                  <div class="img img--class3"></div>
                </router-link>
              </div>
            </div>
            <div class="col-4">
              <div class="classCard">
                <router-link to="/ProductList/" class="card">
                  <div class="img img--class4"></div>1
                </router-link>
              </div>
            </div>
            <div class="col-4">
              <div class="classCard">
                <router-link to="/ProductList/" class="card">
                  <div class="img img--class5"></div>
                </router-link>
              </div>
            </div>
            <div class="col-4">
              <div class="classCard">
                <router-link to="/ProductList/" class="card">
                  <div class="img img--class6"></div>
                </router-link>
              </div>
            </div>
          </div>
          <!-- couponCard -->
          <div class="row bg-dark mb-30 mt-30">
            <div class="col-8">
              <div class="couponCard">
                <h4>Coupon</h4>
                <p></p>
              </div>
            </div>
            <div class="col-4">
              <div class="couponCard">
                <router-link to="/Coupon"></router-link>
                <!-- <a href="#"></a> -->
              </div>
            </div>
          </div>
          <!-- productCard title -->
          <div class="row">
            <div class="col-12">
              <div class="title">
                <div class="name  name--like">
                  <i class="fas fa-heart"></i>
                  <h3></h3>
                </div>
                <router-link to="/ProductList/" class="more"></router-link>
              </div>
            </div>
          </div>
          <!-- productCard -->
          <ul class="row mb-40">
            <li class="col-4" v-for="(item, index) in randomProduct" :key="index">
              <div class="productCard">
                <div class="img" :style="{backgroundImage:`url(${item.imageUrl})`}"></div>
                <div class="detail">
                  <button class="detail__view" @click.prevent="toProductDetail(item.id)">VIEW DETAIL</button>
                </div>
                <div class="content">
                  <h4>{{item.title}}</h4>
                  <p class="group"><i class="fas fa-tag"></i>{{item.category}}</p>
                </div>
              </div>
            </li>
          </ul>
          <!-- trackCard -->
          <div class="row bg-dark mb-40 mt-30">
            <div class="col-6">
              <div class="trackCard">
                <p><br></p>
              </div>
            </div>
            <div class="col-6">
              <div class="trackCard">
                <input type="email" name="email" id="email" placeholder="Email" v-validate="'required|email'"
                  :class="{'error':errors.has('email')}">
                <button @click="trackWebSite"></button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <footer class="footer">
        <div class="link">
          <a href="https://reurl.cc/oD15r3"><i class="fab fa-github"></i></a>
          <a href="https://reurl.cc/9zGRvv"><i class="fab fa-codepen"></i></a>
        </div>
        <p></p>
      </footer>
    </div>
  </div>
</template>

<script>
  import Swiper from './pageModules/Swiper';
  export default {
    components: {
      Swiper,
    },
    data() {
      return {
        // 
        products: [],
        // 
        randomProduct: [],
        // Loading
        effect: {
          isLoading: false,
        },
      }
    },
    methods: {
      // 
      getProductList() {
        const url = `${process.env.API_Server}/api/${process.env.API_Path}/products/all`;
        const vm = this;
        vm.effect.isLoading = true;
        vm.$http.get(url).then((response) => {
          vm.products = response.data.products;
          vm.effect.isLoading = false;
          vm.randomGetProduct();
        })
      },
      // 
      randomGetProduct() {
        const vm = this;
        let productLen = vm.products.length;
        let itemLen = 6;
        let object = {};
        // 
        while (vm.randomProduct.length < itemLen) {
          let r = parseInt(Math.random() * productLen);
          if (!object[r]) {
            object[r] = true;
            vm.randomProduct.push(vm.products[r]);
          }
        }
      },
      // 
      toProductDetail(id) {
        const vm = this;
        vm.$router.push({
          path: `/ProductDetail/${id}`
        });
      },
      // 
      trackWebSite() {
        this.$validator.validate().then(valid => {
          if (valid) {
            this.$bus.$emit('message:push', '', 'success');
          } else {
            this.$bus.$emit('message:push', 'Email', 'danger');
          }
        });
      }
    },
    created() {
      this.getProductList();
    },
  }

</script>



