<template>
  <div class="deliveryMethods">
    <div class="name">
      <i class="fas fa-exclamation-circle"></i>
      <h3></h3>
    </div>
    <ul class="list">
      <li class="list__item">
        <input type="radio" name="sport" id="a1" checked>
        <label for="a1" class="method">
          <p> (7-11) NT$0</p>
          <span>7-1150100</span>
        </label>
      </li>
      <li class="list__item">
        <input type="radio" name="sport" id="a2">
        <label for="a2" class="method">
          <p> () NT$0</p>
          <span>OK50100</span>
        </label>
      </li>
    </ul>
    <button @click.prevent="callnextStep"><i class="fas fa-arrow-right"></i></button>
  </div>
</template>

<script>
  export default {
    methods: {
      callnextStep() {
        this.$emit('nextStep');
      }
    },
  }

</script>



